<template>
  <div class="dropdown-container">
    <button
      @click="showList = !showList"
      @focusout="focusOut"
      class="menu-button"
    >
      <i class="fa fa-bars"></i>
    </button>
    <transition name="ScaleIn">
      <div v-if="showList" class="menu">
        <div
          v-for="(value, key) in options"
          v-bind:key="key"
          class="div-repetitivo"
        >
          <router-link
            class="menu-item"
            :to="value[1]"
            v-if="value[1] !== 'logout'"
          >
            {{ value[0] }}
            <i :class="'fa fa-' + value[2]" aria-hidden="true"></i>
          </router-link>
          <a class="menu-item" @click="$auth.logout()" v-else>
            {{ value[0] }}
            <i :class="'fa fa-' + value[2]" aria-hidden="true"></i>
          </a>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "dropdown",
  props: {
    options: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      showList: {
        type: Boolean,
        default: false
      }
    };
  },
  methods: {
    focusOut() {
      setTimeout(() => {
        this.showList = false;
      }, 20);
    }
  },
  beforeMount() {
    this.showList = false;
  }
};
</script>

<style src="./dropdown.scss" lang="scss" scoped></style>
