<template>
  <div class="main-projects-container">
    <transition-group
      class="projects-container"
      tag="div"
      name="project-animation"
    >
      <div
        class="project-container"
        v-for="project in projects"
        :key="project"
        :class="{ active: selectedProjects.includes(project) }"
        @click="() => (noSelect ? clickHandler(project) : noSelectHandler)"
        v-touch:touchhold="() => touchHoldHandler(project)"
      >
        <span class="project-data-span">PROJECT {{ project }}</span>
      </div>
    </transition-group>
    <transition name="SlideInY">
      <div class="buttons-container" v-show="selectionActive">
        <button class="projects-buttons" @click="markAsFinish">
          <i class="fa fa-check" aria-hidden="true"></i>
        </button>
        <button class="projects-buttons" @click="deleteProjects">
          <i class="fa fa-trash" aria-hidden="true"></i>
        </button>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "projects",
  methods: {
    logout() {
      this.$auth.logout();
    },

    markAsFinish() {
      this.$store.dispatch("selection/MARK_ITEMS_AS_DONE");
    },

    deleteProjects() {
      this.$store.dispatch("selection/DELETE_ITEMS_PROCESS", {
        module: "root",
        state: "projects"
      });
    },

    noSelectHandler() {
      this.$store.commit("selection/SET_NO_SELECT", true);
    },

    clickHandler(project) {
      if (this.selectionActive) {
        this.$store.dispatch("selection/HANDLE_ITEMS_SELECTED", project);
      } else {
        this.goToProject();
      }
    },

    goToProject() {
      if (!this.selectionActive) {
        this.$router.push("/dashboard");
      }
    },

    touchHoldHandler(project) {
      this.$store.dispatch("selection/HANDLE_TOUCH_HOLD_SELECTION", project);
    }
  },
  computed: {
    ...mapState("auth", {
      user: "user"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active",
      selectedProjects: "items_selected",
      noSelect: "no_select"
    }),

    ...mapState({
      projects: state => state.root.projects
    })
  },
  beforeMount() {
    this.$store.commit("SET_NAVBAR_TITLE", "PROJECTS");
    this.$store.commit("SET_PREVIUS_PAGE", "/");
    this.$store.dispatch("selection/RESET_SELECTIONS");
  }
};
</script>

<style src="./projects.scss" lang="scss" scoped></style>
