<template>
  <div class="add-user-container">
    <h1 class="add-title">ADD NEW USER</h1>
    <Card :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="add_user"
        method="post"
        class="forms-container"
      >
        <div class="row-container">
          <div class="row-container">
            <customSelect
              @click="newUserData.role = $event"
              :label="selectLabel"
              :options="roles"
              :icon="'id-badge'"
              required
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="newUserData.name"
              v-bind="{
                identifier: 'name',
                label: 'Name',
                icon: 'user',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="newUserData.lastname"
              v-bind="{
                identifier: 'lastname',
                label: 'Lastname',
                icon: 'user-o',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="newUserData.email"
              v-bind="{
                identifier: 'email',
                label: 'Email',
                icon: 'envelope',
                type: 'email'
              }"
            />
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showSuccess">
              <span class="error-message">
                New user added successfully.
              </span>
            </div>
          </transition>
          <transition name="FadeIn">
            <div class="row-container" v-show="showError">
              <span class="error-message">
                User already register.
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          ADD
        </button>
      </form>
    </Card>
  </div>
</template>

<script>
import { mapState } from "vuex";

import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import customSelect from "@/components/customselect/customselect";

export default {
  name: "addUsers",
  components: {
    Card,
    customInput,
    customSelect
  },
  data() {
    return {
      newUserData: {
        name: "",
        lastname: "",
        email: "",
        role: null
      },
      selectLabel: "Select a role...",
      showError: false,
      showSuccess: false
    };
  },
  methods: {
    handleSubmit() {
      this.showSuccess = false;
      this.showError = false;

      // For testing -------------------------------------
      this.newUserData.username =
        "testUsername" +
        Math.random()
          .toString(36)
          .substring(7);
      // -------------------------------------------------
      console.log(this.newUserData);
      this.axios
        .post("register", this.newUserData)
        .then(e => {
          console.log(e);

          this.showSuccess = true;

          this.newUserData.name = "";
          this.newUserData.lastname = "";
          this.newUserData.email = "";
          this.newUserData.role = null;
          this.selectLabel = "Select a role...";

          setTimeout(() => {
            this.showSuccess = false;
          }, 3000);
        })
        .catch(e => {
          console.log(e);
          this.showError = true;
        });
    }
  },
  computed: {
    ...mapState("roles", {
      roles: "roles_list"
    })
  },
  beforeMount() {
    this.$store.commit("SET_NAVBAR_TITLE", "ADMIN");
    this.$store.dispatch("roles/FETCH_ROLES_LIST");
    this.$store.commit("SET_PREVIUS_PAGE", "/admin/users");
  }
};
</script>

<style src="./addUsers.scss" lang="scss" scoped></style>
