<template>
  <transition name="SlideIn">
    <transition-group
      class="personal-container"
      :key="currentTab"
      tag="div"
      name="person-animation"
    >
      <div
        class="person-container"
        v-for="person in personalList[currentTab]"
        :key="person.id"
        :class="{ active: selectedPersons.includes(person) }"
        @click="() => (noSelect ? clickHandler(person) : noSelectHandler)"
        v-touch:touchhold="() => touchHoldHandler(person)"
      >
        <p>{{ person.title }}</p>
        <p>{{ person.description }}</p>
      </div>
    </transition-group>
  </transition>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "personal",
  methods: {
    noSelectHandler() {
      this.$store.commit("selection/SET_NO_SELECT", true);
    },

    clickHandler(person) {
      if (this.selectionActive) {
        this.$store.dispatch("selection/HANDLE_ITEMS_SELECTED", person);
      } else {
        this.goTo();
      }
    },

    goTo() {
      if (!this.selectionActive) {
        console.log("new route");
      }
    },

    touchHoldHandler(person) {
      this.$store.dispatch("selection/HANDLE_TOUCH_HOLD_SELECTION", person);
    }
  },
  computed: {
    ...mapState("personal", {
      personalList: "personal_list"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active",
      selectedPersons: "items_selected",
      noSelect: "no_select"
    }),

    ...mapState("management", {
      currentTab: "current_tab"
    })
  }
};
</script>

<style src="./personal.scss" lang="scss" scoped></style>
