import Vue from "vue";
import store from "@/store/index";
import router from "@/router/index";
import * as moment from "moment";

Vue.mixin({
  beforeCreate() {
    const options = this.$options;
    if (options.auth) this.$auth = options.auth;
    else if (options.parent && options.parent.$auth)
      this.$auth = options.parent.$auth;
  }
});

const auth = {
  login(credentials) {
    return Vue.axios.post("auth/login/", credentials).then(response => {
      this.setSession(response.data);
      router.push("/projects");
    });
  },

  setSession(data) {
    if (store.state.auth.remember_me) {
      localStorage.setItem("access_token", data.access_token);
      localStorage.setItem("expires_at", data.expires_at);
      localStorage.setItem("user", data.user);
      localStorage.setItem("role", data.user.roles[0].id);
    }

    store.commit("auth/SET_ACCESS_TOKEN", data.access_token);
    store.commit("auth/SET_EXPIRES_AT", data.expires_at);
    store.commit("auth/SET_USER", data.user);
    store.commit("auth/SET_ROLE", data.user.roles[0].id);
  },

  logout() {
    store.dispatch("auth/logout");
  },

  isLoggedIn() {
    return moment().isBefore(this.getExpiration());
  },

  isLoggedOut() {
    return !this.isLoggedIn();
  },

  getExpiration() {
    const expires_at =
      localStorage.getItem("expires_at") || store.state.auth.expire_at;
    return moment(expires_at);
  }
};

export default auth;
