<template>
  <div class="roles-container-main">
    <transition-group class="roles-container" tag="div" name="roles-animation">
      <div
        class="role-container"
        v-for="role in rolesList"
        :key="role.id"
        :class="{ active: selectedRoles.includes(role) }"
        @click="() => (noSelect ? clickHandler(role) : noSelectHandler)"
        v-touch:touchhold="() => touchHoldHandler(role.id)"
      >
        <div class="id-container">{{ role.id }}</div>
        <div class="role-data-container">
          <div class="name-container">{{ role.nombre }}</div>
          <div class="email-container">{{ role.descripcion }}</div>
        </div>
      </div>
    </transition-group>
    <transition name="buttons-bar">
      <div class="buttons-container" v-show="selectionActive">
        <button class="roles-buttons" @click="deleteRoles">
          <i class="fa fa-trash" aria-hidden="true"></i>
        </button>
      </div>
    </transition>
    <transition name="add-animation">
      <router-link
        :to="{ name: 'addRoles' }"
        class="add-button"
        v-if="!selectionActive"
        >+</router-link
      >
    </transition>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "roles",
  methods: {
    deleteRoles() {
      this.$store.dispatch("roles/DELETE_ROLES");
    },

    noSelectHandler() {
      this.$store.commit("selection/SET_NO_SELECT", true);
    },

    clickHandler(role) {
      if (this.selectionActive) {
        this.$store.dispatch("selection/HANDLE_ITEMS_SELECTED", role);
      } else {
        this.goTo(role);
      }
    },

    goTo(role) {
      if (!this.selectionActive) {
        this.$router.push({ name: "editRole", params: { base_role: role } });
      }
    },

    touchHoldHandler(roleid) {
      this.$store.dispatch(
        "selection/HANDLE_TOUCH_HOLD_SELECTION",
        this.rolesList.find(x => x.id === roleid)
      );
    }
  },
  computed: {
    ...mapState("roles", {
      rolesList: "roles_list"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active",
      selectedRoles: "items_selected",
      noSelect: "no_select"
    })
  },
  beforeMount() {
    this.$store.dispatch("roles/FETCH_ROLES_LIST");
    this.$store.dispatch("selection/RESET_SELECTIONS");
    this.$store.commit("SET_NAVBAR_TITLE", "ADMIN");
    this.$store.commit("SET_PREVIUS_PAGE", "/admin");
  }
};
</script>

<style src="./roles.scss" lang="scss" scoped></style>
