<template>
  <div class="management-container">
    <tabs></tabs>
    <div class="components-container" id="components-container">
      <component :is="currentCategory"></component>
    </div>
    <transition name="SlideInY">
      <div class="buttons-container" v-show="selectionActive">
        <button class="management-buttons" @click="deleteEntry">
          <i class="fa fa-trash" aria-hidden="true"></i>
        </button>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState } from "vuex";

import tabs from "@/components/Tabs/tabs";
import tasks from "@/components/tasks/tasks";
import materials from "@/components/materials/materials";
import personal from "@/components/personal/personal";

export default {
  name: "management",
  components: {
    tabs,
    tasks,
    materials,
    personal
  },
  methods: {
    markAsFinish() {
      this.$store.dispatch("selection/MARK_ITEMS_AS_DONE");
    },

    deleteEntry() {
      this.$store.dispatch("selection/DELETE_ITEMS_PROCESS", {
        module: this.$route.params.category,
        state: `${this.$route.params.category}_list`,
        indexes: this.currentTab
      });
    }
  },
  computed: {
    ...mapState("management", {
      currentCategory: "current_category",
      currentTab: "current_tab"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active"
    })
  },
  watch: {
    currentTab: function() {
      var container = this.$el.querySelector("#components-container");
      container.scrollTop = 0;
      this.$store.dispatch("selection/RESET_SELECTIONS");
    }
  },
  beforeMount() {
    this.$store.dispatch(
      this.$store.state.root.category_routes_mapping[
        this.$route.params.category
      ]
    );
    this.$store.commit("SET_PREVIUS_PAGE", "/dashboard");
  }
};
</script>

<style src="./management.scss" lang="scss" scoped></style>
