<template>
  <div class="layout">
    <Navbar v-if="isLoggedIn && $route.name !== 'login'" />
    <article class="content">
      <transition :name="SlideName ? 'SlideInL' : 'SlideInR'">
        <slot></slot>
      </transition>
    </article>
  </div>
</template>

<script>
import Navbar from "@/components/navbar/navbar";

export default {
  name: "layout",
  data: function() {
    return {
      SlideName: false
    };
  },
  components: {
    Navbar
  },
  computed: {
    isLoggedIn() {
      return this.$store.state.auth.user;
    }
  },
  watch: {
    $route() {
      this.SlideName = !this.SlideName;
    }
  }
};
</script>

<style src="./layout.scss" lang="scss" scoped></style>
