<template>
  <nav class="navbar-container">
    <button
      class="back-button"
      @click="goBack"
      v-if="$route.name !== 'projects'"
    >
      <i class="fa fa-arrow-left"></i>
    </button>
    <div class="spacerMobile"></div>
    <span class="title">
      {{ navbarTitle }}
    </span>
    <div class="spacerMobile"></div>
    <dropdown :options="menuOptions"></dropdown>
  </nav>
</template>

<script>
import dropdown from "@/components/dropdown/dropdown";
import { mapState } from "vuex";

export default {
  name: "Navbar",
  components: {
    dropdown
  },
  methods: {
    goBack() {
      if (this.$store.state.root.previus_page == "/") {
        this.$auth.logout();
      } else {
        this.$router.push(this.$store.state.root.previus_page);
      }
    }
  },
  computed: {
    ...mapState({
      menuOptions: state => state.root.menu_options,
      navbarTitle: state => state.root.navbar_title
    })
  }
};
</script>

<style src="./navbar.scss" lang="scss" scoped></style>
