<template>
  <div class="add-role-container">
    <h1 class="add-title">ADD NEW ROLE</h1>
    <Card :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="add_role"
        method="post"
        class="forms-container"
      >
        <div class="row-container">
          <div class="row-container">
            <customInput
              v-model="newRoleData.name"
              v-bind="{
                identifier: 'name',
                label: 'Name',
                icon: 'id-badge',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="newRoleData.description"
              v-bind="{
                identifier: 'description',
                label: 'Description',
                icon: 'commenting-o',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customCheckBox
              style="margin: 10px"
              v-model="newRoleData.createFlag"
              :check_id="'create'"
              :icon="'plus'"
              :name="'create'"
              :label="'CREATE'"
            />
            <customCheckBox
              style="margin: 10px"
              v-model="newRoleData.readFlag"
              :check_id="'read'"
              :icon="'eye'"
              :name="'read'"
              :label="'READ'"
            />
            <customCheckBox
              style="margin: 10px"
              v-model="newRoleData.updateFlag"
              :check_id="'update'"
              :icon="'edit'"
              :name="'update'"
              :label="'UPDATE'"
            />
            <customCheckBox
              style="margin: 10px"
              v-model="newRoleData.deleteFlag"
              :check_id="'delete'"
              :icon="'minus'"
              :name="'delete'"
              :label="'DELETE'"
            />
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showSuccess">
              <span class="error-message">
                New role added successfully.
              </span>
            </div>
          </transition>
          <transition name="FadeIn">
            <div class="row-container" v-show="showError">
              <span class="error-message">
                {{ errorMessage }}
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          ADD
        </button>
      </form>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import customCheckBox from "@/components/customcheckbox/customcheckbox";

export default {
  name: "addRoles",
  components: {
    Card,
    customInput,
    customCheckBox
  },
  data() {
    return {
      newRoleData: {
        name: "",
        description: "",
        createFlag: false,
        readFlag: false,
        deleteFlag: false,
        updateFlag: false,
        permissions: []
      },
      showError: false,
      showSuccess: false,
      errorMessage: "Role already exist."
    };
  },
  methods: {
    handleSubmit() {
      this.showSuccess = false;
      this.showError = false;

      this.newRoleData.createFlag ? this.newRoleData.permissions.push(1) : null;
      this.newRoleData.readFlag ? this.newRoleData.permissions.push(2) : null;
      this.newRoleData.updateFlag ? this.newRoleData.permissions.push(3) : null;
      this.newRoleData.deleteFlag ? this.newRoleData.permissions.push(4) : null;

      if (this.newRoleData.permissions.length < 1) {
        this.errorMessage = "You need to select a least one permission type.";
        this.showError = true;
      } else {
        this.axios
          .post("role", this.newRoleData)
          .then(e => {
            console.log(e);

            this.showSuccess = true;

            this.newRoleData.name = "";
            this.newRoleData.description = "";
            this.newRoleData.createFlag = false;
            this.newRoleData.readFlag = false;
            this.newRoleData.updateFlag = false;
            this.newRoleData.deleteFlag = false;

            setTimeout(() => {
              this.showSuccess = false;
            }, 3000);
          })
          .catch(e => {
            console.log(e);
            this.showError = true;
          });
      }
    }
  },
  beforeMount() {
    this.$store.commit("SET_NAVBAR_TITLE", "ADMIN");
    this.$store.commit("SET_PREVIUS_PAGE", "/admin/roles");
  }
};
</script>

<style src="./addRoles.scss" lang="scss" scoped></style>
