<template>
  <div class="edit-user-container">
    <h1 class="edit-title">EDIT USER</h1>
    <Card :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="edit_user"
        method="post"
        class="forms-container"
      >
        <div class="row-container">
          <div class="row-container">
            <customSelect
              @click="editUserData.role = $event"
              :label="selectLabel"
              :options="roles"
              :icon="'id-badge'"
              required
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="editUserData.name"
              v-bind="{
                identifier: 'name',
                label: 'Name',
                icon: 'user',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="editUserData.lastname"
              v-bind="{
                identifier: 'lastname',
                label: 'Lastname',
                icon: 'user-o',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="editUserData.email"
              v-bind="{
                identifier: 'email',
                label: 'Email',
                icon: 'envelope',
                type: 'email'
              }"
            />
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showSuccess">
              <span class="error-message">
                User edited successfully.
              </span>
            </div>
          </transition>
          <transition name="FadeIn">
            <div class="row-container" v-show="showError">
              <span class="error-message">
                User already register.
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          EDIT
        </button>
      </form>
    </Card>
  </div>
</template>

<script>
import { mapState } from "vuex";

import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import customSelect from "@/components/customselect/customselect";

export default {
  name: "editUsers",
  props: {
    base_user: {
      default() {
        return {
          name: "",
          lastname: "",
          email: "",
          roles: [
            {
              id: "",
              nombre: ""
            }
          ]
        };
      }
    }
  },
  components: {
    Card,
    customInput,
    customSelect
  },
  data() {
    return {
      editUserData: {
        id: this.base_user.id,
        name: this.base_user.name,
        lastname: this.base_user.lastname,
        email: this.base_user.email,
        role: this.base_user.roles[0].id
      },
      selectLabel: this.base_user.roles[0].nombre,
      showError: false,
      showSuccess: false
    };
  },
  methods: {
    handleSubmit() {
      this.showSuccess = false;
      this.showError = false;

      this.axios
        .put(`user/${this.editUserData.id}`, this.editUserData)
        .then(e => {
          console.log(e);

          this.showSuccess = true;

          setTimeout(() => {
            this.showSuccess = false;
          }, 3000);
        })
        .catch(e => {
          console.log(e);
          this.showError = true;
        });
    }
  },

  computed: {
    ...mapState("roles", {
      roles: "roles_list"
    })
  },
  beforeMount() {
    this.$store.commit("SET_NAVBAR_TITLE", "ADMIN");
    this.$store.dispatch("roles/FETCH_ROLES_LIST");
    this.$store.commit("SET_PREVIUS_PAGE", "/admin/users");

    console.log(this.base_user);

    if (!this.base_user.email) {
      this.$router.push(this.$store.state.root.previus_page);
    }
  }
};
</script>

<style src="./editUsers.scss" lang="scss" scoped></style>
