<template>
  <div class="ConfirmEmail-container">
    <h1 class="confirm-title">
      EMAIL CONFIRMATION
    </h1>
    <transition name="FadeIn" mode="out-in">
      <div key="checking" v-if="!showChecking && !showError">
        CHECKING...
      </div>
      <div
        key="confirmed"
        class="confirmed-message"
        v-else-if="showChecking && !showError"
      >
        Your email has been confirmed. Another email has been sent to you, click
        <router-link to="/">here</router-link> to go to the login page.
      </div>
      <div key="Error" class="error-message">
        Somthing when wrong, try again.
        <button key="button" @click="confirmEmail" class="boton-confirm">
          CONFIRM
        </button>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "ConfirmEmail",
  data() {
    return {
      showChecking: false,
      showError: false
    };
  },
  methods: {
    confirmEmail() {
      this.showError = false;
      this.showChecking = false;

      this.axios
        .get(this.$route.query.url)
        .then(e => {
          console.log(e);
          this.showChecking = true;
        })
        .catch(e => {
          console.log(e);
          this.showError = true;
        });
    }
  },
  mounted() {
    console.log(this.$route.query.url);
    this.confirmEmail();
  }
};
</script>

<style src="./ConfirmEmail.scss" lang="scss" scoped></style>
