<template>
  <transition name="SlideIn">
    <transition-group
      class="tasks-container"
      :key="currentTab"
      tag="div"
      name="task-animation"
    >
      <div
        class="task-container"
        v-for="task in tasksList[currentTab]"
        :key="task.id"
        :class="{ active: selectedTasks.includes(task) }"
        @click="() => (noSelect ? clickHandler(task) : noSelectHandler)"
        v-touch:touchhold="() => touchHoldHandler(task)"
      >
        <p>{{ task.title }}</p>
        <p>{{ task.description }}</p>
      </div>
    </transition-group>
  </transition>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "tasks",
  methods: {
    noSelectHandler() {
      this.$store.commit("selection/SET_NO_SELECT", true);
    },

    clickHandler(task) {
      if (this.selectionActive) {
        this.$store.dispatch("selection/HANDLE_ITEMS_SELECTED", task);
      } else {
        this.goTo();
      }
    },

    goTo() {
      if (!this.selectionActive) {
        console.log("new route");
      }
    },

    touchHoldHandler(task) {
      this.$store.dispatch("selection/HANDLE_TOUCH_HOLD_SELECTION", task);
    }
  },
  computed: {
    ...mapState("tasks", {
      tasksList: "tasks_list"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active",
      selectedTasks: "items_selected",
      noSelect: "no_select"
    }),

    ...mapState("management", {
      currentTab: "current_tab"
    })
  }
};
</script>

<style src="./tasks.scss" lang="scss" scoped></style>
