<template>
  <label class="container">
    <input
      type="checkbox"
      :name="name"
      :id="check_id"
      :checked="value"
      @change="$emit('input', $event.target.checked)"
      class="hidden"
    />
    <span for="check_id" class="checkmark"></span>
    {{ label }}
    <i :class="'fa fa-' + icon" aria-hidden="true"></i>
  </label>
</template>

<script>
export default {
  name: "customCheckBox",
  props: {
    value: {
      default: false
    },
    label: {
      type: String,
      default: "checkbox"
    },
    icon: {
      type: String,
      default: ""
    },
    name: {
      type: String,
      default: "myCheckBox"
    },
    check_id: {
      default: "myCheckBox"
    }
  }
};
</script>

<style src="./customcheckbox.scss" lang="scss" scoped></style>
