<template>
  <div class="users-container-main">
    <transition-group class="users-container" tag="div" name="users-animation">
      <div
        class="user-container"
        v-for="user in usersList"
        :key="user.id"
        :class="{ active: selectedUsers.includes(user) }"
        @click="() => (noSelect ? clickHandler(user) : noSelectHandler)"
        v-touch:touchhold="() => touchHoldHandler(user.id)"
      >
        <div class="id-container">{{ user.id }}</div>
        <div class="user-data-container">
          <div class="name-container">{{ user.name }} {{ user.lastname }}</div>
          <div class="email-container">{{ user.email }}</div>
        </div>
      </div>
    </transition-group>
    <transition name="buttons-bar">
      <div class="buttons-container" v-show="selectionActive">
        <button class="users-buttons" @click="deleteUsers">
          <i class="fa fa-trash" aria-hidden="true"></i>
        </button>
      </div>
    </transition>
    <transition name="add-animation">
      <router-link
        :to="{ name: 'addUsers' }"
        class="add-button"
        v-if="!selectionActive"
        >+</router-link
      >
    </transition>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "users",
  methods: {
    deleteUsers() {
      this.$store.dispatch("users/DELETE_USERS");
    },

    noSelectHandler() {
      this.$store.commit("selection/SET_NO_SELECT", true);
    },

    clickHandler(user) {
      if (this.selectionActive) {
        this.$store.dispatch("selection/HANDLE_ITEMS_SELECTED", user);
      } else {
        this.goTo(user);
      }
    },

    goTo(user) {
      if (!this.selectionActive) {
        console.log(user);
        this.$router.push({ name: "editUsers", params: { base_user: user } });
      }
    },

    touchHoldHandler(userid) {
      this.$store.dispatch(
        "selection/HANDLE_TOUCH_HOLD_SELECTION",
        this.usersList.find(x => x.id === userid)
      );
    }
  },
  computed: {
    ...mapState("users", {
      usersList: "users_list"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active",
      selectedUsers: "items_selected",
      noSelect: "no_select"
    })
  },
  beforeMount() {
    this.$store.dispatch("users/FETCH_USERS_LIST");
    this.$store.dispatch("selection/RESET_SELECTIONS");
    this.$store.commit("SET_NAVBAR_TITLE", "ADMIN");
    this.$store.commit("SET_PREVIUS_PAGE", "/admin");
  }
};
</script>

<style src="./users.scss" lang="scss" scoped></style>
