<template>
  <div class="reset-container">
    <Card :shadow="false">
      <h2 class="reset-title">
         {{ $t('new_password') | upperCase }}
      </h2>
      <form
        @submit.prevent="handleSubmit"
        name="reset"
        method="post"
        class="forms-container"
      >
        <div class="column-container">
          <div class="row-container">
            <customInput
              v-model="resetData.password"
              v-bind="{
                identifier: 'password-reset',
                label: $t('password'),
                icon: 'lock',
                type: 'password'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="resetData.password_confirmation"
              v-bind="{
                identifier: 'password-reset-confirmation',
                label: $t('password_confirmation'),
                icon: 'unlock-alt',
                type: 'password'
              }"
            />
          </div>
          <div class="column-container" v-show="isDisabled">
            <span class="indications"> {{ $t('entries_match')}}</span>
            <span class="indications" style="margin-bottom: 20px;">
              {{ $t('password_long')}}</span
            >
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showMessage">
              <span class="error-message">
                {{ $t(message)}}
              </span>
            </div>
          </transition>
        </div>
        <button
          class="boton-enviar"
          :disabled="isDisabled"
          :class="{ disable: isDisabled }"
        >
          {{ $t('reset') | upperCase }}
        </button>
        <div class="row-container">
          <router-link class="return-login" to="/">{{ $t('return_login')}}</router-link>
        </div>
      </form>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import { mapState } from "vuex";

export default {
  name: "resetToken",
  components: {
    Card,
    customInput
  },
  data() {
    return {
      resetData: {
        password: "",
        password_confirmation: "",
        token: this.$route.params.token
      },
      message: ""
    };
  },
  computed: {
    isDisabled() {
      return (
        this.resetData.password_confirmation !== this.resetData.password ||
        this.resetData.password.length < 7
      );
    },

    ...mapState({
      showMessage: state => state.root.reset_message
    })
  },
  methods: {
    handleSubmit() {
      this.$store.commit("SET_RESET_MESSAGE", false);
      console.log(
        this.resetData.password === this.resetData.password_confirmation
      );
      setTimeout(() => {
        this.axios
          .put("auth/reset/password", this.resetData)
          .then(e => {
            console.log(e);

            this.message = "new_password_success";
            this.$store.commit("SET_RESET_MESSAGE", true);
            this.resetData.password = "";
            this.resetData.password_confirmation = "";

            setTimeout(() => {
              this.$router.push("/");
            }, 4000);
          })
          .catch(e => {
            if (e.response) {
              if (e.response.status == 404) {
                this.message = "invalid_token"
              } else {
                this.message = "something_wrong"
              }
            } else {
              this.message = "connection_error"
            }
            this.$store.commit("SET_RESET_MESSAGE", true);
          });
      }, 200);
    }
  },
  beforeMount() {
    this.$store.commit("SET_RESET_MESSAGE", false);
  }
};
</script>

<style src="./resetToken.scss" lang="scss" scoped></style>
