import axios from "axios";

export default {
  namespaced: true,

  state: () => ({
    roles_list: []
  }),

  mutations: {
    SET_ROLES_LIST(state, roles_list) {
      state.roles_list = roles_list;
    },

    DELETE_ROLES_LIST(state, role) {
      state.roles_list.splice(state.roles_list.indexOf(role), 1);
    }
  },

  actions: {
    FETCH_ROLES_LIST({ commit }) {
      axios.get("roles").then(e => {
        console.log(e);
        commit("SET_ROLES_LIST", e.data.data);
      });
    },

    DELETE_ROLES({ commit, rootState }) {
      rootState.selection.items_selected.forEach(element => {
        axios
          .delete(`role/${element.id}`)
          .then(e => {
            console.log(e);
            commit("DELETE_ROLES_LIST", element);
            commit("selection/REMOVE_ITEMS_SELECTED", element, {
              root: true
            });
          })
          .catch(e => console.log(e));
      });
    }
  },

  getters: {}
};
