export default {
  namespaced: true,

  state: () => ({
    tabs_list: [
      { id: "todo", label: "TODO" },
      { id: "in_process", label: "IN PROCESS" },
      { id: "done", label: "DONE" }
    ],
    current_tab: 0,
    current_category: "tasks"
  }),

  mutations: {
    SET_CURRENT_TAB(state, currentTab) {
      state.current_tab = currentTab;
    },

    SET_TABS_LIST(state, tabs_list) {
      state.tabs_list = tabs_list;
    },

    SET_CURRENT_CATEGORY(state, current_category) {
      state.current_category = current_category;
    }
  },

  actions: {},

  getters: {}
};
