<template>
  <div class="tabs-container">
    <div class="tab-container" v-for="(tab, index) in tabsList" :key="index">
      <input
        type="radio"
        :checked="currentTab == index"
        class="tab-button"
        :class="{ 'active-tab': currentTab == index }"
        @click="changeTab(index)"
        name="grupo"
      />
      <span class="tab-label">{{ tab.label }}</span>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "tabs",
  computed: {
    ...mapState("management", {
      tabsList: "tabs_list",
      currentTab: "current_tab"
    })
  },
  methods: {
    changeTab(currentTab) {
      this.$store.commit("management/SET_CURRENT_TAB", currentTab);
    }
  }
};
</script>

<style src="./tabs.scss" lang="scss" scoped></style>
