<template>
  <div class="dropdown-container">
    <button
      @click.prevent="showList = !showList"
      @focusout="focusOut"
      class="menu-button"
    >
      <input
        class="hidden-input"
        type="text"
        :value="inputValue || Number.isInteger(label)"
        required
      />
      <transition name="FadeIn">
        <span v-if="label" :key="labelData">{{ labelData }}</span>
      </transition>
    </button>
    <i
      v-if="icon"
      :class="['fa fa-' + icon + ' input-icons', { active: showList }]"
    ></i>
    <transition name="ScaleIn">
      <div v-if="showList" class="menu" :class="{ 'menu-no-icon': !icon }">
        <div
          v-for="(value, key) in options"
          v-bind:key="key"
          class="div-repetitivo"
        >
          <span class="menu-item" @click="selectedItemHandler(value)">
            {{ value.nombre }}
          </span>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "customSelect",
  props: {
    options: {
      type: Array,
      default: null
    },
    icon: {
      type: String,
      default: ""
    },
    label: {
      type: String,
      default: "select..."
    }
  },
  data() {
    return {
      showList: {
        type: Boolean,
        default: false
      },
      labelData: this.label,
      inputValue: null
    };
  },
  methods: {
    focusOut() {
      setTimeout(() => {
        this.showList = false;
      }, 100);
    },
    selectedItemHandler(value) {
      this.labelData = value.nombre;
      this.inputValue = true;
      this.$emit("click", value.id);
    }
  },
  beforeMount() {
    this.showList = false;
  }
};
</script>

<style src="./customselect.scss" lang="scss" scoped></style>
