<template>
  <div class="edit-role-container">
    <h1 class="edit-title">ADD NEW ROLE</h1>
    <Card :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="edit_role"
        method="post"
        class="forms-container"
      >
        <div class="row-container">
          <div class="row-container">
            <customInput
              v-model="editRoleData.name"
              v-bind="{
                identifier: 'name',
                label: 'Name',
                icon: 'id-badge',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="editRoleData.description"
              v-bind="{
                identifier: 'description',
                label: 'Description',
                icon: 'commenting-o',
                type: 'text'
              }"
            />
          </div>
          <div class="row-container">
            <customCheckBox
              style="margin: 10px"
              v-model="editRoleData.createFlag"
              :check_id="'create'"
              :icon="'plus'"
              :name="'create'"
              :label="'CREATE'"
            />
            <customCheckBox
              style="margin: 10px"
              v-model="editRoleData.readFlag"
              :check_id="'read'"
              :icon="'eye'"
              :name="'read'"
              :label="'READ'"
            />
            <customCheckBox
              style="margin: 10px"
              v-model="editRoleData.updateFlag"
              :check_id="'update'"
              :icon="'edit'"
              :name="'update'"
              :label="'UPDATE'"
            />
            <customCheckBox
              style="margin: 10px"
              v-model="editRoleData.deleteFlag"
              :check_id="'delete'"
              :icon="'minus'"
              :name="'delete'"
              :label="'DELETE'"
            />
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showSuccess">
              <span class="error-message">
                Role edited successfully.
              </span>
            </div>
          </transition>
          <transition name="FadeIn">
            <div class="row-container" v-show="showError">
              <span class="error-message">
                {{ errorMessage }}
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          EDIT
        </button>
      </form>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import customCheckBox from "@/components/customcheckbox/customcheckbox";

export default {
  name: "editRole",
  props: {
    base_role: {
      default() {
        return {
          editRoleData: {
            name: "",
            description: "",
            permissions: []
          }
        };
      }
    }
  },
  components: {
    Card,
    customInput,
    customCheckBox
  },
  data() {
    return {
      editRoleData: {
        id: this.base_role.id,
        name: this.base_role.nombre,
        description: this.base_role.descripcion,
        createFlag: false,
        readFlag: false,
        deleteFlag: false,
        updateFlag: false,
        permissions: []
      },
      showError: false,
      showSuccess: false,
      errorMessage: "Role already exist."
    };
  },
  methods: {
    handleSubmit() {
      this.showSuccess = false;
      this.showError = false;

      this.editRoleData.createFlag
        ? this.editRoleData.permissions.push(1)
        : null;
      this.editRoleData.readFlag ? this.editRoleData.permissions.push(2) : null;
      this.editRoleData.updateFlag
        ? this.editRoleData.permissions.push(3)
        : null;
      this.editRoleData.deleteFlag
        ? this.editRoleData.permissions.push(4)
        : null;

      if (this.editRoleData.permissions.length < 1) {
        this.errorMessage = "You need to select a least one permission type.";
        this.showError = true;
      } else {
        this.axios
          .put(`role/${this.editRoleData.id}`, this.editRoleData)
          .then(e => {
            console.log(e);

            this.showSuccess = true;

            setTimeout(() => {
              this.showSuccess = false;
            }, 3000);
          })
          .catch(e => {
            console.log(e);
            this.showError = true;
          });
      }
    }
  },
  beforeMount() {
    this.$store.commit("SET_NAVBAR_TITLE", "ADMIN");
    this.$store.commit("SET_PREVIUS_PAGE", "/admin/roles");

    if (!this.base_role.nombre) {
      this.$router.push(this.$store.state.root.previus_page);
    } else {
      this.base_role.permissions.forEach(element => {
        element.id == 1 ? (this.editRoleData.createFlag = true) : null;
        element.id == 2 ? (this.editRoleData.readFlag = true) : null;
        element.id == 3 ? (this.editRoleData.updateFlag = true) : null;
        element.id == 4 ? (this.editRoleData.deleteFlag = true) : null;
      });
    }
  }
};
</script>

<style src="./editRole.scss" lang="scss" scoped></style>
