import axios from "axios";
import router from "@/router/index";

export default {
  namespaced: true,

  state: () => ({
    user: localStorage.getItem("user") || null,
    access_token: localStorage.getItem("access_token") || "",
    expire_at: localStorage.getItem("expires_at") || undefined,
    role: localStorage.getItem("role") || "",
    showErrorMessageLogin: false,
    remember_me: false
  }),

  mutations: {
    SET_USER(state, payload) {
      state.user = payload;
    },

    SET_ACCESS_TOKEN(state, payload) {
      state.access_token = payload;
    },

    SET_REMEMBER_ME(state, flag) {
      state.remember_me = flag;
    },

    SET_EXPIRES_AT(state, payload) {
      state.expire_at = payload;
    },

    SET_ROLE(state, role) {
      state.role = role;
    },

    SET_SHOW_ERROR_MESSAGE_LOGIN(state, payload) {
      state.showErrorMessageLogin = payload;
    }
  },

  actions: {
    async logout(state) {
      return await axios
        .get("auth/logout")
        .then(e => {
          console.log(e);
          localStorage.removeItem("user");
          localStorage.removeItem("access_token");
          localStorage.removeItem("expires_at");
          localStorage.removeItem("role");
          state.commit("SET_USER", null);
          state.commit("SET_ACCESS_TOKEN", "");
          state.commit("SET_EXPIRES_AT", "");
          state.commit("SET_ROLE", "");
          router.push("/");
        })
        .catch(e => {
          console.log(e);
          localStorage.removeItem("user");
          localStorage.removeItem("access_token");
          localStorage.removeItem("expires_at");
          localStorage.removeItem("role");
          state.commit("SET_USER", null);
          state.commit("SET_ACCESS_TOKEN", "");
          state.commit("SET_EXPIRES_AT", "");
          state.commit("SET_ROLE", "");
          router.push("/");
        });
    }
  },

  getters: {}
};
