import Vue from "vue";
import VueRouter from "vue-router";
import dashboard from "@/views/dashboard/dashboard";
import projects from "@/views/projects/projects";
import management from "@/views/management/management";
import login from "@/views/login/login";
import main_admin from "@/views/admin/main_admin/main_admin";
import users from "@/views/admin/users/users";
import roles from "@/views/admin/roles/roles";
import addUsers from "@/views/admin/addUsers/addUsers";
import addRoles from "@/views/admin/addRoles/addRoles";
import editUsers from "@/views/admin/editUsers/editUsers";
import editRole from "@/views/admin/editRole/editRole";
import ConfirmEmail from "@/views/ConfirmEmail/ConfirmEmail";
import resetPassword from "@/views/resetPassword/resetPassword";
import resetToken from "@/views/resetPassword/resetToken";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "login",
    component: login,
    beforeEnter
  },
  {
    path: "/ConfirmEmail",
    name: "ConfirmEmail",
    component: ConfirmEmail
  },
  {
    path: "/reset-password",
    name: "resetPassword",
    component: resetPassword
  },
  {
    path: "/reset-password/:token",
    name: "resetToken",
    component: resetToken
  },
  {
    path: "/dashboard",
    name: "dashboard",
    component: dashboard,
    beforeEnter
  },
  {
    path: "/management/:category",
    name: "management",
    component: management,
    beforeEnter
  },
  {
    path: "/projects",
    name: "projects",
    component: projects,
    beforeEnter
  },
  {
    path: "/admin",
    name: "main_admin",
    component: main_admin,
    beforeEnter
  },
  {
    path: "/admin/users",
    name: "users",
    component: users,
    beforeEnter
  },
  {
    path: "/admin/users/add-user",
    name: "addUsers",
    component: addUsers,
    beforeEnter
  },
  {
    path: "/admin/users/edit",
    name: "editUsers",
    component: editUsers,
    props: true,
    beforeEnter
  },
  {
    path: "/admin/roles",
    name: "roles",
    component: roles,
    beforeEnter
  },
  {
    path: "/admin/roles/add-role",
    name: "addRoles",
    component: addRoles,
    beforeEnter
  },
  {
    path: "/admin/roles/edit",
    name: "editRole",
    component: editRole,
    props: true,
    beforeEnter
  }
];

const router = new VueRouter({
  mode: "history",
  routes
});

function beforeEnter(to, from, next) {
  if (router.app.$auth.isLoggedOut() && to.path !== "/") {
    next("/");
  } else if (router.app.$auth.isLoggedIn() && to.path == "/") {
    next("/projects");
  } else {
    next();
  }
}

export default router;
