<template>
  <div class="input-container">
    <input
      v-bind:value="value"
      v-on:input="validators"
      :id="identifier"
      :name="identifier"
      :required="required"
      :type="type"
      class="input-el"
      v-on:change="validators"
      enabled
      :class="{
        validInput: validInput && type == 'email' && value.length > 0,
        invalidInput: !validInput && type == 'email' && value.length > 0
      }"
    />
    <label :for="identifier" class="input-label">{{
      required ? label + " *" : label + " (opcional)"
    }}</label>
    <i :class="'fa fa-' + icon + ' input-icons'"></i>
  </div>
</template>

<script>
export default {
  name: "customInput",
  data() {
    return {
      validInput: {
        type: Boolean,
        default: false
      }
    };
  },
  props: {
    value: {
      type: String
    },
    identifier: {
      type: String
    },
    label: {
      type: String
    },
    icon: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: true
    },
    type: {
      type: String,
      default: "text"
    }
  },
  methods: {
    validators: function($event) {
      this.$emit("input", $event.target.value);
      if (this.type == "email" && $event.target.value.length > 0) {
        this.validInput = this.validEmail($event.target.value);
      }
    },
    validEmail: function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }
  },
  mounted() {
    this.validInput = false;
  }
};
</script>

<style src="./custominput.scss" lang="scss" scoped></style>
