<template>
  <div class="login-container">
    <Card :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="login"
        method="post"
        class="forms-container"
      >
        <div class="column-container">
          <img
            src="/images/cropped-Avonce_Logo_header-4.png"
            alt="Logo"
            class="logo-login"
          />
          <div class="row-container">
            <customInput
              v-model="loginData.email"
              v-bind="{
                identifier: 'email',
                label: $t('email'),
                icon: 'envelope',
                type: 'email'
              }"
            />
          </div>
          <div class="row-container">
            <customInput
              v-model="loginData.password"
              v-bind="{
                identifier: 'password',
                label: $t('password'),
                icon: 'lock',
                type: 'password'
              }"
            />
          </div>
          <!-- <div class="row-container">
            <customCheckBox
              v-model="loginData.remember_me"
              :label="'Remember me?'"
              :icon="'history'"
            />
          </div> -->
          <transition name="FadeIn">
            <div class="row-container" v-show="showError">
              <span class="error-message">
                {{ $t(message) }}
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          {{ $t('send') | upperCase }}
        </button>
        <button @click.prevent="idioma">switch</button>
        <div class="row-container">
          <router-link class="reset-password" to="/reset-password"
            > {{  $t('forgot_password') }} </router-link
          >
        </div>
      </form>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
// import customCheckBox from "@/components/customcheckbox/customcheckbox";

import { mapState } from "vuex";

export default {
  name: "login",
  components: {
    Card,
    customInput
  },
  data() {
    return {
      loginData: {
        email: "",
        password: "",
        remember_me: true
      },
      message: "",
      flag: false,
    };
  },
  computed: {
    ...mapState("auth", {
      showError: "showErrorMessageLogin"
    })
  },
  methods: {
    handleSubmit() {
      this.$store.commit("auth/SET_SHOW_ERROR_MESSAGE_LOGIN", false);
      this.$store.commit("auth/SET_REMEMBER_ME", this.loginData.remember_me);
      setTimeout(() => {
        let response = this.$auth.login(this.loginData);
        response.catch(e => {
          if (e.response) {
            if (e.response.status == 401) {
              this.message = "wrong_user"
            } else {
              this.message = "something_wrong"
            }
          } else {
            this.message = "connection_error"
          }

          this.$store.commit("auth/SET_REMEMBER_ME", false);
          this.$store.commit("auth/SET_SHOW_ERROR_MESSAGE_LOGIN", true);
        });
      }, 200);
    },

    idioma() {
      if (this.flag) {
        this.flag = !this.flag
        this.$i18n.locale = "es"
      } else {
        this.flag = !this.flag
        this.$i18n.locale = "en"
      }
    }
  },
  watch: {
    $route() {
      if (this.$store.state.root.previus_page) {
        this.$auth.logout();
      }
    }
  }
};
</script>

<style src="./login.scss" lang="scss" scoped></style>
