<template>
  <transition name="SlideIn">
    <transition-group
      class="materials-container"
      :key="currentTab"
      tag="div"
      name="material-animation"
    >
      <div
        class="material-container"
        v-for="material in materialsList[currentTab]"
        :key="material.id"
        :class="{ active: selectedMaterials.includes(material) }"
        @click="() => (noSelect ? clickHandler(material) : noSelectHandler)"
        v-touch:touchhold="() => touchHoldHandler(material)"
      >
        <p>{{ material.title }}</p>
        <p>{{ material.description }}</p>
      </div>
    </transition-group>
  </transition>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "materials",
  methods: {
    noSelectHandler() {
      this.$store.commit("selection/SET_NO_SELECT", true);
    },

    clickHandler(material) {
      if (this.selectionActive) {
        this.$store.dispatch("selection/HANDLE_ITEMS_SELECTED", material);
      } else {
        this.goTo();
      }
    },

    goTo() {
      if (!this.selectionActive) {
        console.log("new route");
      }
    },

    touchHoldHandler(material) {
      this.$store.dispatch("selection/HANDLE_TOUCH_HOLD_SELECTION", material);
    }
  },
  computed: {
    ...mapState("materials", {
      materialsList: "materials_list"
    }),

    ...mapState("selection", {
      selectionActive: "selection_active",
      selectedMaterials: "items_selected",
      noSelect: "no_select"
    }),

    ...mapState("management", {
      currentTab: "current_tab"
    })
  }
};
</script>

<style src="./materials.scss" lang="scss" scoped></style>
