export default {
  namespaced: true,

  state: () => ({
    selection_active: false,
    items_selected: [],
    no_select: true
  }),

  mutations: {
    SET_ITEMS_SELECTED(state, item) {
      state.items_selected.push(item);
    },

    REMOVE_ITEMS_SELECTED(state, item) {
      state.items_selected.splice(state.items_selected.indexOf(item), 1);
    },

    SET_NO_SELECT(state, flag) {
      state.no_select = flag;
    },

    SET_SELECTION_ACTIVE(state, active) {
      state.selection_active = active;
    },

    CLEAR_ITEMS_SELECTED(state) {
      state.items_selected = [];
    },

    DELETE_ITEMS(state, payload) {
      let module = payload.var_state.module;
      let state_var = payload.var_state.state;

      let array_indexes =
        payload.var_state.indexes !== undefined
          ? payload.var_state.indexes
          : undefined;

      let rootState = payload.rootState;

      if (array_indexes !== undefined) {
        state.items_selected.forEach(element => {
          rootState[module][state_var][array_indexes].splice(
            rootState[module][state_var][array_indexes].indexOf(element),
            1
          );
        });
      } else {
        state.items_selected.forEach(element => {
          rootState[module][state_var].splice(
            rootState[module][state_var].indexOf(element),
            1
          );
        });
      }
    }
  },

  actions: {
    RESET_SELECTIONS({ commit }) {
      commit("CLEAR_ITEMS_SELECTED");
      commit("SET_SELECTION_ACTIVE", false);
      commit("SET_NO_SELECT", true);
    },

    HANDLE_TOUCH_HOLD_SELECTION({ commit, dispatch }, item) {
      commit("SET_SELECTION_ACTIVE", true);
      commit("SET_NO_SELECT", false);

      dispatch("HANDLE_ITEMS_SELECTED", item);

      setTimeout(() => {
        commit("SET_NO_SELECT", true);
      }, 700);
    },

    HANDLE_ITEMS_SELECTED({ commit, state }, item) {
      state.items_selected.includes(item)
        ? commit("REMOVE_ITEMS_SELECTED", item)
        : commit("SET_ITEMS_SELECTED", item);
    },

    DELETE_ITEMS_PROCESS({ commit, rootState }, var_state) {
      commit("DELETE_ITEMS", {
        var_state: var_state,
        rootState: rootState
      });
      commit("CLEAR_ITEMS_SELECTED");
    },

    MARK_ITEMS_AS_DONE({ commit }) {
      commit("CLEAR_ITEMS_SELECTED");
      alert("Selected projects have been marked as completed!");
    }
  },

  getters: {}
};
