import axios from "axios";

export default {
  namespaced: true,

  state: () => ({
    users_list: []
  }),

  mutations: {
    SET_USERS_LIST(state, users_list) {
      state.users_list = users_list;
    },

    DELETE_USERS_LIST(state, user) {
      state.users_list.splice(state.users_list.indexOf(user), 1);
    }
  },

  actions: {
    FETCH_USERS_LIST({ commit }) {
      axios.get("user").then(e => {
        commit("SET_USERS_LIST", e.data.data);
      });
    },

    DELETE_USERS({ commit, rootState }) {
      rootState.selection.items_selected.forEach(element => {
        axios
          .delete(`user/${element.id}`)
          .then(e => {
            console.log(e);
            commit("DELETE_USERS_LIST", element);
            commit("selection/REMOVE_ITEMS_SELECTED", element, {
              root: true
            });
          })
          .catch(e => console.log(e));
      });
    }
  },

  getters: {}
};
