<template>
  <div class="reset-container">
    <Card :shadow="false">
      <h2 class="reset-title">
        {{ $t('reset_password') | upperCase }}
      </h2>
      <form
        @submit.prevent="handleSubmit"
        name="reset"
        method="post"
        class="forms-container"
      >
        <div class="column-container">
          <div class="row-container">
            <customInput
              v-model="resetData.email"
              v-bind="{
                identifier: 'email-reset',
                label: $t('email'),
                icon: 'envelope',
                type: 'email'
              }"
            />
          </div>
          <transition name="FadeIn">
            <div class="row-container" v-show="showMessage">
              <span class="error-message">
                {{ $t(message) }}
              </span>
            </div>
          </transition>
        </div>
        <button class="boton-enviar">
          {{ $t('send') | upperCase }}
        </button>
        <div class="row-container">
          <router-link class="return-login" to="/">{{ $t('return_login') }}</router-link>
        </div>
      </form>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/card/card";
import customInput from "@/components/custominput/custominput";
import { mapState } from "vuex";

export default {
  name: "resetPassword",
  components: {
    Card,
    customInput
  },
  data() {
    return {
      resetData: {
        email: ""
      },
      message: ""
    };
  },
  computed: {
    ...mapState({
      showMessage: state => state.root.reset_message
    })
  },
  methods: {
    handleSubmit() {
      this.$store.commit("SET_RESET_MESSAGE", false);
      setTimeout(() => {
        this.axios
          .post("auth/reset-password", this.resetData)
          .then(e => {
            console.log(e);
            this.message = "email_password_reset";
            this.$store.commit("SET_RESET_MESSAGE", true);
          })
          .catch(e => {
            if (e.response) {
              if (e.response.status == 404) {
                this.message = "wrong_email"
              } else {
                this.message = "something_wrong"
              }
            } else {
              this.message = "connection_error"
            }

            this.$store.commit("SET_RESET_MESSAGE", true);
          });
      }, 200);
    }
  },
  beforeMount() {
    this.$store.commit("SET_RESET_MESSAGE", false);
  }
};
</script>

<style src="./resetPassword.scss" lang="scss" scoped></style>
